/* Modern Design System, variables, and utility classes */
:root {
  --accent: #3b82f6;
  --text-primary: #1f2937;
  --text-secondary: #6b7280;
  --text-muted: #9ca3af;
  --border-light: #e5e7eb;
  --border-focus: var(--accent);
  --bg-white: #ffffff;
  --bg-gray-50: #f9fafb;
  --bg-gray-100: #f3f4f6;
  --shadow-sm: 0 1px 2px rgba(0, 0, 0, 0.05);
  --shadow-md: 0 4px 6px rgba(0, 0, 0, 0.1);
  --radius-sm: 4px;
  --radius-md: 6px;
  --radius-lg: 8px;
  --radius-xl: 12px;
  --spacing-xs: 0.25rem;
  --spacing-sm: 0.5rem;
  --spacing-md: 1rem;
  --spacing-lg: 1.5rem;
  --spacing-xl: 2rem;
}

body, html, .App { font-family: -apple-system,BlinkMacSystemFont,'Segoe UI',Roboto,'Helvetica Neue',Arial,sans-serif; }
.App {
  min-height: 100vh;
  background: var(--bg-gray-50);
}
.form-container {
  max-width: 400px;
  margin: 0 auto 2rem auto;
  background: white;
  padding: 2rem;
  box-shadow: var(--shadow-md);
  border-radius: var(--radius-xl);
}
.btn-primary {
  background: var(--accent);
  color: white;
  border: 1px solid var(--accent);
  padding: 0.5rem 1rem;
  border-radius: var(--radius-md);
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}
.btn-primary:hover {
  background: rgba(59,130,246,0.9);
  transform: translateY(-1px);
}
.btn-outline {
  background: transparent;
  color: var(--accent);
  border: 1px solid var(--accent);
  padding: 0.5rem 1rem;
  border-radius: var(--radius-md);
}
.btn-outline:hover {
  background: var(--accent);
  color: white;
}
.error {
  border-color: #ef4444;
  color: #dc2626;
  font-size: 0.95em;
  margin-top: 2px;
  margin-bottom: 2px;
}
.success {
  color: #22c55e;
  font-size: 1em;
}
.empty-state {
  text-align: center;
  color: var(--text-muted);
  font-style: italic;
  padding: 2rem 0;
}
input, textarea, select {
  width: 100%;
  padding: 0.5rem;
  border: 1px solid var(--border-light);
  border-radius: var(--radius-md);
  font-size: 1rem;
  transition: all 0.2s;
}
input:focus, textarea:focus, select:focus {
  outline: none;
  border-color: var(--border-focus);
  box-shadow: 0 0 0 3px rgba(59,130,246,0.1);
}
input.error, textarea.error {
  border-color: #ef4444;
  background: #fef2f2;
  color: #dc2626;
}
label {
  display: block;
  font-size: 0.94rem;
  font-weight: 500;
  color: var(--text-secondary);
  margin-bottom: 0.25rem;
  letter-spacing: 0.025em;
}
.workout-table {
  width: 100%;
  background: white;
  border-collapse: collapse;
  border-radius: 12px;
  box-shadow: var(--shadow-md);
}
.workout-table th, .workout-table td {
  padding: 12px 10px;
  text-align: left;
  border-bottom: 1px solid var(--border-light);
}
.workout-table th:first-child { border-radius: 12px 0 0 0; }
.workout-table th:last-child { border-radius: 0 12px 0 0; }
.workout-table tr:last-child td { border-bottom: none; }

@media (max-width: 360px) {
  .form-container {
    padding: 1rem;
    margin: 0.5rem;
  }
  input, textarea, select {
    padding: 0.375rem;
    font-size: 0.9rem;
  }
}
